const Content = () => (
  <div className="grid">
  
  <div className="module">SERIES</div>
  <div className="module">MOVIES</div>

      <style>{`
          
        
.grid {
  display: flex;
  flex-wrap: wrap;
  padding-top : 10%;

  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: minmax(150px, auto);
  grid-gap: 1em;
}

.module {
  background: black;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  
  margin-left: 5px;
  margin-right: 5px;
  flex: 1 1 200px;
}

/* If Grid is supported, remove the margin we set for the fallback */
@supports (display: grid) {
  .module {
    margin: 0;
  }
}


      `}</style>
 </div>

  );
  
  export default Content;